import React from 'react';
import styles from '../styles/order.module.css';

const OrderPage = () => {
  return (
    <div className={`container mt-5`}>
      <h1 className={`${styles.heading} text-center mb-4`}>Order Management</h1>
      
      <div className="row">
        <div className="col-md-12">
          <div className="table-responsive">
            <table className={`table table-striped ${styles.table}`}>
              <thead>
                <tr>
                  <th>Order ID</th>
                  <th>Customer Name</th>
                  <th>Date of Order</th>
                  <th>Status</th>
                  <th>Action</th>
                </tr>
              </thead>
              <tbody>
                <tr>
                  <td>#12345</td>
                  <td>John Doe</td>
                  <td>2024-12-28</td>
                  <td>
                    <span className={`badge bg-warning ${styles.badge}`}>Pending</span>
                  </td>
                  <td>
                    <button className={`btn btn-info btn-sm ${styles.btnInfo}`}>View Details</button>
                    <button className={`btn btn-success btn-sm ms-2 ${styles.btnSm}`}>Update Status</button>
                    <button className={`btn btn-danger btn-sm ms-2 ${styles.btnSm}`}>Cancel</button>
                  </td>
                </tr>
                {/* Repeat for more orders */}
              </tbody>
            </table>
          </div>
        </div>
      </div>
    </div>
  );
};

export default OrderPage;
